<template>
    <div class="container-pokemon">
        <NavbarList/>
        <SearchField @pokemonSearch="handleSearch"/>
        <div class="card" style="display: flex; flex-wrap:wrap;">
            <div class="teste" style="padding: 5px;" v-for="pokemon in pokemons" :key="pokemon">
                <ContentLoader v-if="loading" width="150px" height="300px" primaryColor="#a9a9a9"></ContentLoader>
                <PokemonCard 
                    :pokemonName="pokemon.name"
                    :pokemonUrl="pokemon.url">
                </PokemonCard>
            </div>
            <!-- <div> -->
                <!-- <ul class="flex flex-row flex-gap-2 justify-content-between flex-wrap"> -->
                    <!-- <li v-for="pokemon in pokemons" :key="pokemon"> -->
                        
                    <!-- </li> -->
                <!-- </ul> -->
            <!-- </div> -->
            <FooterContainer @previousPage="previousPage" @nextPage="nextPage"/>
        </div>
    </div>
</template>

<script>
import NavbarList from './NavbarList.vue';
import SearchField from './SearchField.vue';
import PokemonCard from './PokemonCard.vue';
import FooterContainer from '../UI/FooterContainer.vue';
import { ContentLoader } from 'vue-content-loader'
export default {
    components: { PokemonCard, NavbarList, SearchField, ContentLoader, FooterContainer },
    data() {
        return {
            pokemons: [],
            pageNumber: 0,
            loading: true,
            lastSearh: ''
        };
    },
    methods: {
        getListPokemon() {
            fetch("https://pokeapi.co/api/v2/pokemon?limit=20&offset="+this.pageNumber, {
                method: "GET"
            }).then(res => {
                res.json().then(data => ({
                    data: data,
                    status: res.status
                })).then(res => {
                    if(res.status === 200) {
                        this.loading = false;
                    }
                    this.pokemons = [];
                    this.pokemons = res.data.results;
                });
            });
        },
        nextPage() {
            this.pageNumber += 20;
            this.getListPokemon();
            if(this.pokemons <= 0) {
                this.previousPage();
            }
        },
        previousPage() {
            if((this.pageNumber - 20) < 0) {
                return;
            }
            this.pageNumber -= 20;
            this.getListPokemon();
        },
        handleSearch(search) {
            if(this.lastSearh === search) {
                return;
            }
            if(search == '') {
                this.getListPokemon();
            } else {
                fetch('https://pokeapi.co/api/v2/pokemon/'+search, {
                    method: "GET"
                }).then(res => {
                    res.json().then(data => ({
                        data: data,
                        status: res.status
                    })).then(res => {
                        this.pokemons = [];
                        let pokemon = {
                            name: res.data.name,
                            url: 'https://pokeapi.co/api/v2/pokemon/'+search
                        }
                        this.pokemons.push(pokemon);
                    });
                });
            }
        }
    },
    beforeMount() {
        this.getListPokemon();
    }
}
</script>
