    @media(max-width: 380px) {
        .teste {
            width: 50%;
        }
    }
    @media(min-width: 380px) and (max-width: 460px) {
        .teste {
            width: 33.3%;
        }
    }
    .container-pokemon {
        margin-left: 1rem;
        margin-right: 1rem;
        background-color: #F7F7F7;
        border-radius: 6px;
    }
</style>